export interface VideoProps {
  keyCount: number
  noteSize: {
    w: number
    h: number
  }
  noteSpeedMultiplier: number
  judgementLinePos : {
    x: number,
    y: number
  }
  bpm: number
  audioSync: number
}

export function createDefaultVideoProps (bpm: number, keyCount: number, videoWidth: number) {
  const scale = videoWidth / 1920.0
  let noteSizeW = 500 * scale
  switch (keyCount) {
    case 4: noteSizeW /= 4; break
    case 5: noteSizeW /= 5; break
    case 6: case 8: noteSizeW /= 6; break
    default: throw new Error('Bad keyCount')
  }
  return {
    keyCount,
    noteSize: { w: noteSizeW, h: 26 * scale },
    noteSpeedMultiplier: 5.4,
    judgementLinePos: {
      x: 708 * scale,
      y: 752 * scale
    },
    bpm,
    audioSync: 0
  }
}
