import { Lane } from './KeyLog'

export type KeyConfig = { [key: string]: Lane }
function defaultKeymap (): { [key: number]: KeyConfig} {
  return {
    4: {
      a_0: Lane.Lane1,
      s_0: Lane.Lane2,
      ';_0': Lane.Lane3,
      '\'_0': Lane.Lane4,
      shift_1: Lane.SideL,
      shift_2: Lane.SideR
    },

    5: {
      a_0: Lane.Lane1,
      s_0: Lane.Lane2,
      d_0: Lane.Lane3,
      l_0: Lane.Lane3,
      ';_0': Lane.Lane4,
      '\'_0': Lane.Lane5,
      shift_1: Lane.SideL,
      shift_2: Lane.SideR
    },

    6: {
      a_0: Lane.Lane1,
      s_0: Lane.Lane2,
      d_0: Lane.Lane3,
      l_0: Lane.Lane4,
      ';_0': Lane.Lane5,
      '\'_0': Lane.Lane6,
      shift_1: Lane.SideL,
      shift_2: Lane.SideR
    },

    8: {
      a_0: Lane.Lane1,
      s_0: Lane.Lane2,
      d_0: Lane.Lane3,
      l_0: Lane.Lane4,
      ';_0': Lane.Lane5,
      '\'_0': Lane.Lane6,
      c_0: Lane.TriggerL,
      ',_0': Lane.TriggerR,
      shift_1: Lane.SideL,
      shift_2: Lane.SideR
    }
  }
}

const kKeyMap = 'kKeyMap'
function loadKeymap (): { [key: number]: KeyConfig} {
  const pref = localStorage.getItem(kKeyMap)
  if (pref) return JSON.parse(pref)
  else return defaultKeymap()
}

let keyConfig = loadKeymap()

export function clearKeyConfig () {
  localStorage.removeItem(kKeyMap)
  keyConfig = loadKeymap()
}

export function setKeyConfig (keyCount: number, config: KeyConfig) {
  keyConfig[keyCount] = config
  localStorage.setItem(kKeyMap, JSON.stringify(keyConfig))
}

export function getLaneKeyMap (keyCount: number) {
  return keyConfig[keyCount]
}
