import { onMounted, onBeforeUnmount } from 'vue'

export function onKeyDown (handler: (key: string) => boolean) {
  onMounted(() => {
    document.addEventListener('keydown', listener)
  })

  onBeforeUnmount(() => {
    document.removeEventListener('keydown', listener)
  })

  function listener (ev: KeyboardEvent) {
    const keyString = `${ev.key}_${ev.location}`
    if (handler(keyString.toLowerCase())) {
      ev.stopPropagation()
      ev.preventDefault()
    }
  }
}
