import { onBeforeUnmount, onMounted, Ref, ref } from 'vue'

export function registerResizeWatcher (elementRef: Ref, handler: () => void) {
  const ro = ref(null) as Ref<ResizeObserver | null>

  onMounted(() => {
    const ob = new ResizeObserver(onResize)
    ob.observe(elementRef.value!)
    ro.value = ob

    handler()
  })

  onBeforeUnmount(() => {
    ro.value?.unobserve(elementRef.value!)
  })

  function onResize () {
    handler()
  }
}
