import { VideoProps } from '@/VideoProps'
import { KeyLog, Lane } from '@/KeyLog'

export function updateNoteOverlayCanvas (
  videoElement: HTMLVideoElement,
  targetCanvas: HTMLCanvasElement,
  videoProps: VideoProps,
  keyLog: KeyLog
) {
  const ctx = targetCanvas.getContext('2d')!
  const { width, height } = targetCanvas
  const currentTime = videoElement.currentTime + videoProps.audioSync
  const { judgementLinePos, noteSize } = videoProps
  const nodeTime = 60.0 / (videoProps.bpm / 4.0)
  const yPerSecond = judgementLinePos.y / 2 / (nodeTime) * videoProps.noteSpeedMultiplier
  const perfectRange = 0.042
  const perfectY = perfectRange * yPerSecond

  ctx.clearRect(0, 0, width, height)

  const noteRegionWidth = videoProps.noteSize.w * 6

  // darken non-note-regions.
  ctx.fillStyle = '#000000e0'
  ctx.rect(0, 0, judgementLinePos.x, height)
  ctx.rect(judgementLinePos.x + noteRegionWidth, 0, width, height)
  ctx.fill()

  // Draw note region boundary
  ctx.strokeStyle = 'blue'
  ctx.lineWidth = 5
  ctx.beginPath()

  ctx.moveTo(judgementLinePos.x, 0)
  ctx.lineTo(judgementLinePos.x, height)

  ctx.moveTo(judgementLinePos.x + noteRegionWidth, 0)
  ctx.lineTo(judgementLinePos.x + noteRegionWidth, height)

  ctx.moveTo(judgementLinePos.x, judgementLinePos.y)
  ctx.lineTo(judgementLinePos.x + noteRegionWidth, judgementLinePos.y)

  ctx.stroke()

  // render notes
  for (const keyEntry of keyLog) {
    // Assumes 6b
    const rectH = noteSize.h
    let rectW: number
    let sideNoteW: number
    switch (videoProps.keyCount) {
      case 4: sideNoteW = noteSize.w * 2; break
      case 5: sideNoteW = noteSize.w * 2.5; break
      case 6: case 8: sideNoteW = noteSize.w * 3; break
      default: throw new Error('Invalid keyCount')
    }

    if (
      keyEntry.lane === Lane.SideL ||
      keyEntry.lane === Lane.SideR ||
      keyEntry.lane === Lane.TriggerL ||
      keyEntry.lane === Lane.TriggerR
    ) {
      rectW = sideNoteW
    } else {
      rectW = noteSize.w
    }

    let rectX = judgementLinePos.x
    switch (keyEntry.lane) {
      case Lane.Lane1:
      case Lane.Lane2:
      case Lane.Lane3:
      case Lane.Lane4:
      case Lane.Lane5:
      case Lane.Lane6:
        rectX += noteSize.w * (keyEntry.lane - Lane.Lane1)
        ctx.fillStyle = '#ffff00a0'
        break

      case Lane.SideL:
        ctx.fillStyle = '#00ff00a0'
        break

      case Lane.SideR:
        rectX += sideNoteW
        ctx.fillStyle = '#00ff00a0'
        break

      case Lane.TriggerL:
        ctx.fillStyle = '#ff0000a0'
        break

      case Lane.TriggerR:
        rectX += sideNoteW
        ctx.fillStyle = '#ff0000a0'
        break
    }

    // Some black magic

    const rectY = judgementLinePos.y - noteSize.h / 2 - yPerSecond * (keyEntry.time - currentTime)

    if (rectY < -noteSize.h || rectY > height) continue

    // Render
    ctx.beginPath()
    ctx.rect(rectX, rectY, rectW, rectH)
    ctx.fill()

    // Render perfect range
    ctx.beginPath()
    ctx.strokeStyle = '#ffffffc0'
    ctx.lineWidth = 5
    ctx.moveTo(rectX + rectW / 2, rectY - perfectY + rectH / 2)
    ctx.lineTo(rectX + rectW / 2, rectY + perfectY + rectH / 2)
    ctx.stroke()
  }
}
