export const audioCtx = new (
  window.AudioContext || // Default
  (window as any).webkitAudioContext || // Safari and old versions of Chrome
  false
)()

function readFileArrayBuffer (file: File): Promise<ArrayBuffer> {
  return new Promise((resolve) => {
    const reader = new FileReader()
    reader.onload = function () {
      const arrayBuffer = reader.result as ArrayBuffer
      resolve(arrayBuffer)
    }
    reader.readAsArrayBuffer(file)
  })
}

export async function getAudioBufferFromFile (file: File): Promise<AudioBuffer> {
  const arrayBuffer = await readFileArrayBuffer(file)
  const decodedAudioData = await audioCtx.decodeAudioData(arrayBuffer)
  return decodedAudioData
}
